ul {
  margin: 0;
  list-style: none;
  padding: 0; }

@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

.course {
  grid-area: course; }

.education {
  grid-area: education; }

.experience {
  grid-area: experience; }

.experience_work {
  margin: 0 0 0.5rem 0;
  font-size: 1rem; }

.experience_period {
  margin: 0 0 0.3rem 0;
  color: #21baab; }

.experience_duties {
  margin: 0 0 2rem 0; }

.experience_duties li::before {
  content: "•";
  color: #21baab;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  padding-right: 10px; }

.general_skill {
  grid-area: general_skill; }

.resume_header {
  display: flex; }

.interest {
  grid-area: interest; }

.language {
  grid-area: language; }

h2 {
  font-size: 2rem;
  font-weight: 200;
  margin-bottom: 0; }

h3 {
  font-size: 1.5rem;
  color: #21baab;
  font-weight: 100;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem; }

a {
  text-decoration: none;
  color: black; }

.photo_image {
  border: 5px solid #21baab;
  border-radius: 50%; }

.resume_header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  padding: 2rem;
  border-bottom: 2px solid #21baab; }

.name {
  width: 30%; }

.contact li {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 0.3rem; }

.contact li:hover {
  color: #21baab; }

.contact svg {
  margin-left: 0.5rem; }

.contact svg:hover {
  color: #21baab; }

.resume {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas: "experience general_skill" "experience soft_skill" "experience language" "experience course" "education interest";
  margin: 2rem 7.3rem; }

.visually-hidden {
  visibility: hidden; }

.html {
  font-size: 10px; }

h4 {
  text-transform: uppercase;
  font-size: 1.2rem;
  margin: 0 0 1rem 0; }

h5 {
  font-size: 1rem;
  font-weight: bold;
  margin: 0 0 0.7rem 0; }

.soft_skill {
  grid-area: soft_skill; }
